.container {
  @include push--auto();
  max-width: 1080px;
  width: 100%;
}

.layout {
  min-height: 300px;
  margin-top: 20px;
  background: $color-white;
  padding: 20px;
}

@media all and (min-width: 768px) {
  .main-content {
    float: left; /* LTR */
    position: relative;
  }
  .two-sidebars {
    .main-content {
      width: 50%;
      padding: 0 10px;
    }
    .layout {
      @include flexbox();
    }
  }
  .one-sidebar {
    .main-content {
      width: 75%;
    }
    .layout {
      @include flexbox();
    }
  }
  .no-sidebars {
    .main-content {
      width: 100%;
    }
  }
  .sidebar-first {
    .main-content {
      margin-right: 0; /* LTR */
      padding: 0 0 0 10px;
    }
  }
  .sidebar-second {
    .main-content {
      margin-left: 0;
      padding: 0 10px 0 0;
    }
  }
}

