.quick__links {
  background: $quick-link-bg;
  padding: 1px 0;
  a {
    font-size: 12px;
  }
}

.main__menu {
  background: $main-menu-bg;
  height: 55px;
  @include flexbox();
  .region-menu {
    @include flexbox();
    @include align-items(center);
    @include justify-content(space-between);
    height: 100%;
  }
  ul.menu {
    a {
      color: $color-white;
      font-size: 14px;
      &.is-active, &:hover {
        font-family: $font-secondary-bold;
        color: $color-white;
      }
    }
  }
}

header {
  text-transform: uppercase;
  font-family: $font-secondary;
  .container {
    padding: 0;
  }
}
