/**
 * @file
 * Styles for Bartik's buttons.
 */
.ad__section {
  background: #333;
  padding-top: 20px;
  padding-bottom: 25px;
}

ul.menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  margin-left: 0;
}

ul.menu li {
  padding: 0;
  margin-right: 25px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1080px;
  width: 100%;
}

.layout {
  min-height: 300px;
  margin-top: 20px;
  background: #fff;
  padding: 20px;
}

@media all and (min-width: 768px) {
  .main-content {
    float: left;
    /* LTR */
    position: relative;
  }
  .two-sidebars .main-content {
    width: 50%;
    padding: 0 10px;
  }
  .two-sidebars .layout {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
  }
  .one-sidebar .main-content {
    width: 75%;
  }
  .one-sidebar .layout {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
  }
  .no-sidebars .main-content {
    width: 100%;
  }
  .sidebar-first .main-content {
    margin-right: 0;
    /* LTR */
    padding: 0 0 0 10px;
  }
  .sidebar-second .main-content {
    margin-left: 0;
    padding: 0 10px 0 0;
  }
}

.quick__links {
  background: #e4e4e4;
  padding: 1px 0;
}

.quick__links a {
  font-size: 12px;
}

.main__menu {
  background: #ed1c24;
  height: 55px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
}

.main__menu .region-menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  -moz-justify-content: space-between;
  justify-content: space-between;
  height: 100%;
}

.main__menu ul.menu a {
  color: #fff;
  font-size: 14px;
}

.main__menu ul.menu a.is-active, .main__menu ul.menu a:hover {
  font-family: "OpenSansBold", sans-serif;
  color: #fff;
}

header {
  text-transform: uppercase;
  font-family: "OpenSansRegular", sans-serif;
}

header .container {
  padding: 0;
}

/* ------------------ Sidebar ----------------- */
@media all and (min-width: 768px) {
  .one-sidebar .sidebar {
    width: 25%;
  }
  #sidebar-first {
    width: 25%;
    padding-right: 10px;
  }
  #sidebar-second {
    width: 25%;
    padding-left: 10px;
  }
}

footer .footer__top {
  background: #f2f2f2;
  padding: 30px 0;
}

footer .footer__bottom {
  padding: 20px 0;
}
